import { NgModule } from '@angular/core'
import { RouterModule } from '@angular/router'
import {
    MatButtonModule,
    MatCheckboxModule,
    MatFormFieldModule,
    MatIconModule,
    MatInputModule,
    MatProgressBarModule,

    MatDialogModule,
} from '@angular/material'

import { FuseSharedModule } from '@fuse/shared.module'

// Directive Only Number Text Input
import { NumberOnlyDirective } from './number.directive'

import { RecaptchaModule } from 'ng-recaptcha'

import { LoginService } from 'app/services/login.service'

import { LoginComponent } from 'app/login/login.component'
import { DialogForgotPassword } from './dialog/dialog-forgot-password/dialog-forgot-password'
import { DialogPolicy } from './dialog/dialog-policy/dialog-policy'

import { DialogMessage } from 'app/dialog/dialog-message/dialog-message'

const routes = [
    {
        path: 'login',
        component: LoginComponent
    }
]

@NgModule({
    declarations: [
        NumberOnlyDirective,

        LoginComponent,
        DialogForgotPassword,
        DialogPolicy,

        DialogMessage
    ],
    entryComponents: [
        DialogForgotPassword,
        DialogPolicy,

        DialogMessage
    ],
    imports: [
        RouterModule.forChild(routes),

        MatButtonModule,
        MatCheckboxModule,
        MatFormFieldModule,
        MatIconModule,
        MatInputModule,
        MatProgressBarModule,

        MatDialogModule,

        FuseSharedModule,
        RecaptchaModule
    ],
    providers: [
        LoginService
    ]
})
export class LoginModule {
}
