import { Component, OnInit, ViewEncapsulation } from '@angular/core'
import { FormBuilder, FormGroup, FormControl } from '@angular/forms'
import { FuseProgressBarService } from '@fuse/components/progress-bar/progress-bar.service'
import { HttpClient } from '@angular/common/http'
import { MatDialog } from '@angular/material'
import { FuseConfigService } from '@fuse/services/config.service'
import { fuseAnimations } from '@fuse/animations'
import { Router } from '@angular/router'
import * as crypto from 'crypto-js'

import { LoginService } from '../services/login.service'
import { UtilityService } from '../services/utility.service'

import { DialogForgotPassword } from './dialog/dialog-forgot-password/dialog-forgot-password'
import { DialogPolicy } from './dialog/dialog-policy/dialog-policy'

@Component({
    selector: 'login',
    templateUrl: './login.component.html',
    styleUrls: ['./login.component.scss'],
    providers: [UtilityService, LoginService],
    encapsulation: ViewEncapsulation.None,
    animations: fuseAnimations
})
export class LoginComponent implements OnInit {
    isRememberMe: boolean

    isShowPassword: boolean
    isLoading: boolean

    site_key = "6Le6ymQUAAAAAJVDIBTS82-eNLk8tlQQyzwlCQKQ"

    response_captcha: string
    token: string

    loginForm: FormGroup

    username = new FormControl('')
    password = new FormControl('')

    constructor(
        private fuseConfigService: FuseConfigService,
        private fuseProgressBarService: FuseProgressBarService,
        private utilityService: UtilityService,
        private loginService: LoginService,
        private http: HttpClient,
        private formBuilder: FormBuilder,
        private dialog: MatDialog,
        private router: Router
    ) {
        this.isRememberMe = false

        this.isShowPassword = false
        this.isLoading = false

        this.loginForm = this.formBuilder.group({
            username: this.username,
            password: this.password
        })

        this.fuseConfigService.config = {
            layout: {
                navbar: {
                    hidden: true
                },
                toolbar: {
                    hidden: true
                },
                footer: {
                    hidden: true
                },
                sidepanel: {
                    hidden: true
                }
            }
        }
    }

    ngOnInit(): void {
        localStorage.removeItem('cva')
        sessionStorage.removeItem('cva')
    }

    resolved(captchaResponse: string) {
        console.log(`Resolved captcha with response: ${captchaResponse}`)
        this.response_captcha = captchaResponse
    }

    startCondition() {
        this.fuseProgressBarService.hide()
        this.isLoading = false
        this.response_captcha = undefined
        grecaptcha.reset()
    }

    errorCondition(timeout: any, title: string, message: string, button: string) {
        clearTimeout(timeout)

        this.startCondition()

        this.utilityService.dialogMessage(title, message, button)
    }

    timeoutCondition(hitApi: any, timeout: any) {
        hitApi.unsubscribe()
        clearTimeout(timeout)

        this.startCondition()

        this.utilityService.dialogMessage("Pesan", "Timeout, Mohon periksa koneksi internet anda", "Oke")
    }

    loginFunc() {
        this.fuseProgressBarService.show()
        this.isLoading = true

        let ParamReq = {
            login: this.username.value,
            password: this.password.value,
            // responscaptcha: this.response_captcha
        }

        let Login = this.loginService.Login(ParamReq)
            .subscribe((result: any) => {
                if (result.status == 200) {
                    clearTimeout(timeout)

                    this.startCondition()

                    if (this.isRememberMe) {
                        var encryptProfile = crypto.AES.encrypt(JSON.stringify({
                            user_id: result.data.user.id,
                            name: result.data.user.name,
                            email: result.data.user.email,
                            username: result.data.user.username,
                            user_role_id: result.data.user.user_role_id,
                            user_phone: result.data.user.user_phone,
                            user_image: result.data.user.user_image,
                            account_bank: result.data.user.account_bank,
                            account_bank_name: result.data.user.account_bank_name,
                            bank_name: result.data.user.bank_name,
                            company_name: result.data.user.company_name,
                            vendor_id: result.data.user.vendor_id,
                            token: result.data.token
                        }), 'WilkaETruck2019').toString()
                        localStorage.setItem('cva', encryptProfile)
                    } else {
                        var encryptProfile = crypto.AES.encrypt(JSON.stringify({
                            user_id: result.data.user.id,
                            name: result.data.user.name,
                            email: result.data.user.email,
                            username: result.data.user.username,
                            role: result.data.user.user_role_id,
                            user_phone: result.data.user.user_phone,
                            user_image: result.data.user.user_image,
                            account_bank: result.data.user.account_bank,
                            account_bank_name: result.data.user.account_bank_name,
                            bank_name: result.data.user.bank_name,
                            company_name: result.data.user.company_name,
                            vendor_id: result.data.user.vendor_id,
                            token: result.data.token
                        }), 'WilkaETruck2019').toString()
                        sessionStorage.setItem('cva', encryptProfile)
                    }

                    this.router.navigate(['/dashboard'])
                } else {
                    this.errorCondition(timeout, "Pesan", "Maaf, Username / No. Handphone yang dimasukan tidak sesuai", "Oke")
                }
            }, _error => {
                if (_error.status == 0) {
                    this.errorCondition(timeout, "Pesan", "Maaf, tidak dapat menghubungkan ke server", "Oke")
                } else if (_error.status == 401) {
                    this.errorCondition(timeout, "Pesan", "Maaf, Username / No. Handphone yang dimasukan tidak sesuai", "Oke")
                } else {
                    this.errorCondition(timeout, "Pesan", "Maaf, sepertinya telah terjadi kesalahan server, mohon coba beberapa saat lagi", "Oke")
                }
            })

        let timeout = setTimeout(() => {
            this.timeoutCondition(Login, timeout)
        }, 10000)
    }

    dialogForgotPassword() {
        this.dialog.open(DialogForgotPassword, {
            width: '600px',
            disableClose: true
        })
    }

    dialogPolicy(statusModal: string) {
        this.dialog.open(DialogPolicy, {
            width: '900px',
            disableClose: true,
            data: {
                statusModal: statusModal
            }
        })
    }
}