import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

import { UtilityService } from './utility.service';

@Injectable()
export class ForgotPasswordService {
    constructor(
        private _http: HttpClient,
        private utilityService: UtilityService
    ) { }

    reqOTP(ParamReq: any) {
        return this._http
            .post(this.utilityService.server + "/api/forget-password/request-otp", ParamReq)
    }

    submitOTP(ParamReq: any) {
        return this._http
            .post(this.utilityService.server + "/api/forget-password/submit-otp", ParamReq)
    }

    newPassword(ParamReq: any) {
        return this._http
            .post(this.utilityService.server + "/api/forget-password/submit-password", ParamReq)
    }
}
