import { Injectable } from '@angular/core'
import { MatDialog } from '@angular/material'
import * as crypto from 'crypto-js'

import { DialogMessage } from 'app/dialog/dialog-message/dialog-message'

@Injectable()
export class UtilityService {
    public user_id: string
    public name: string
    public email: string
    public username: string
    public user_role_id: string
    public user_phone: string
    public user_image: string
    public account_bank: string
    public account_bank_name: string
    public bank_name: string
    public company_name: string
    public vendor_id: string
    public token: string

    public server = "http://localhost:8080"

    constructor(
        private dialog: MatDialog
    ) {
        if (localStorage.getItem('cva')) {
            var bytes = crypto.AES.decrypt(localStorage.getItem('cva'), 'WilkaETruck2019')
            var decryptProfile = bytes.toString(crypto.enc.Utf8)
            var currentUser = JSON.parse(decryptProfile)
            if (currentUser != null) {
                this.user_id = currentUser.user_id
                this.name = currentUser.name
                this.email = currentUser.email
                this.username = currentUser.username
                this.user_role_id = currentUser.user_role_id
                this.user_phone = currentUser.user_phone
                this.user_image = currentUser.user_image
                this.account_bank = currentUser.account_bank
                this.account_bank_name = currentUser.account_bank_name
                this.bank_name = currentUser.bank_name
                this.company_name = currentUser.company_name
                this.vendor_id = currentUser.vendor_id
                this.token = currentUser.token
            }
        } else if (sessionStorage.getItem('cva')) {
            var bytes = crypto.AES.decrypt(sessionStorage.getItem('cva'), 'WilkaETruck2019')
            var decryptProfile = bytes.toString(crypto.enc.Utf8)
            var currentUser = JSON.parse(decryptProfile)
            if (currentUser != null) {
                this.user_id = currentUser.user_id
                this.name = currentUser.name
                this.email = currentUser.email
                this.username = currentUser.username
                this.user_role_id = currentUser.user_role_id
                this.user_phone = currentUser.user_phone
                this.user_image = currentUser.user_image
                this.account_bank = currentUser.account_bank
                this.account_bank_name = currentUser.account_bank_name
                this.bank_name = currentUser.bank_name
                this.company_name = currentUser.company_name
                this.vendor_id = currentUser.vendor_id
                this.token = currentUser.token
            }
        }
    }

    dialogMessage(title: string, message: string, button: string) {
        this.dialog.open(DialogMessage, {
            disableClose: true,
            data: {
                title: title,
                message: message,
                button: button
            }
        })
    }
}