import { Component, OnDestroy, OnInit, ViewEncapsulation } from '@angular/core'
import { Subject } from 'rxjs'
import { takeUntil } from 'rxjs/operators'
import { TranslateService } from '@ngx-translate/core'
import { MatDialog } from '@angular/material'
import { MatIconRegistry } from '@angular/material'
import { DomSanitizer } from '@angular/platform-browser'
import * as _ from 'lodash'

import { FuseConfigService } from '@fuse/services/config.service'
import { FuseSidebarService } from '@fuse/components/sidebar/sidebar.service'

import { Router } from '@angular/router'

import { navigation } from 'app/navigation/navigation'

import { UtilityService } from 'app/services/utility.service'

import { DialogChangePassword } from './dialog/dialog-change-password'

@Component({
    selector: 'toolbar',
    templateUrl: './toolbar.component.html',
    styleUrls: ['./toolbar.component.scss'],
    providers: [UtilityService],
    encapsulation: ViewEncapsulation.None
})

export class ToolbarComponent implements OnInit, OnDestroy {
    horizontalNavbar: boolean
    rightNavbar: boolean
    hiddenNavbar: boolean
    languages: any
    navigation: any
    selectedLanguage: any
    userStatusOptions: any[]

    name: string
    email: string
    username: string
    user_role_id: string
    user_image: string

    // Private
    private _unsubscribeAll: Subject<any>

    /**
     * Constructor
     *
     * @param {FuseConfigService} _fuseConfigService
     * @param {FuseSidebarService} _fuseSidebarService
     * @param {TranslateService} _translateService
     */
    constructor(
        private _fuseConfigService: FuseConfigService,
        private _fuseSidebarService: FuseSidebarService,
        private _translateService: TranslateService,

        private router: Router,

        private matIconRegistry: MatIconRegistry,
        private sanitizer: DomSanitizer,

        private utilityService: UtilityService,

        private dialog: MatDialog,
    ) {
        this.name = this.utilityService.name
        this.email = this.utilityService.email
        this.username = this.utilityService.username
        this.user_role_id = this.utilityService.user_role_id
        this.user_image = this.utilityService.user_image

        // Set the defaults
        this.userStatusOptions = [
            {
                'title': 'Online',
                'icon': 'icon-checkbox-marked-circle',
                'color': '#4CAF50'
            },
            {
                'title': 'Away',
                'icon': 'icon-clock',
                'color': '#FFC107'
            },
            {
                'title': 'Do not Disturb',
                'icon': 'icon-minus-circle',
                'color': '#F44336'
            },
            {
                'title': 'Invisible',
                'icon': 'icon-checkbox-blank-circle-outline',
                'color': '#BDBDBD'
            },
            {
                'title': 'Offline',
                'icon': 'icon-checkbox-blank-circle-outline',
                'color': '#616161'
            }
        ]

        this.languages = [
            {
                id: 'en',
                title: 'English',
                flag: 'us'
            },
            {
                id: 'tr',
                title: 'Turkish',
                flag: 'tr'
            }
        ]

        this.navigation = navigation

        // Set the private defaults
        this._unsubscribeAll = new Subject()
    }

    // -----------------------------------------------------------------------------------------------------
    // @ Lifecycle hooks
    // -----------------------------------------------------------------------------------------------------

    /**
     * On init
     */
    ngOnInit(): void {

        // Subscribe to the config changes
        this._fuseConfigService.config
            .pipe(takeUntil(this._unsubscribeAll))
            .subscribe((settings: any) => {
                this.horizontalNavbar = settings.layout.navbar.position === 'top'
                this.rightNavbar = settings.layout.navbar.position === 'right'
                this.hiddenNavbar = settings.layout.navbar.hidden === true
            })

        // Set the selected language from default languages
        this.selectedLanguage = _.find(this.languages, { 'id': this._translateService.currentLang })
    }

    /**
     * On destroy
     */
    ngOnDestroy(): void {
        // Unsubscribe from all subscriptions
        this._unsubscribeAll.next()
        this._unsubscribeAll.complete()
    }

    // -----------------------------------------------------------------------------------------------------
    // @ Public methods
    // -----------------------------------------------------------------------------------------------------

    /**
     * Toggle sidebar open
     *
     * @param key
     */
    toggleSidebarOpen(key: any): void {
        this._fuseSidebarService.getSidebar(key).toggleOpen()
    }

    /**
     * Search
     *
     * @param value
     */
    search(value: any): void {
        // Do your search here...
    }

    /**
     * Set the language
     *
     * @param lang
     */
    setLanguage(lang: any): void {
        // Set the selected language for the toolbar
        this.selectedLanguage = lang

        // Use the selected language for translations
        this._translateService.use(lang.id)
    }

    dialogChangePassword() {
        this.dialog.open(DialogChangePassword, {
            width: '500px',
            disableClose: true,
        })
    }

    gotoMyProfile() {
        this.router.navigate(["/profile"])
    }
}