import { Component, ViewEncapsulation, Inject } from "@angular/core"
import { fuseAnimations } from "@fuse/animations"
import { FormGroup, FormControl, FormBuilder } from "@angular/forms"
import { MAT_DIALOG_DATA, MatDialogRef } from "@angular/material"
import { Router } from "@angular/router"

import { UtilityService } from "app/services/utility.service"
import { ChangePasswordService } from "app/services/change-password.service"

@Component({
    selector: 'dialog-change-password',
    templateUrl: 'dialog-change-password.html',
    providers: [UtilityService, ChangePasswordService],
    encapsulation: ViewEncapsulation.None,
    animations: fuseAnimations
})
export class DialogChangePassword {
    isShowOldPassword: boolean
    isShowNewPassword: boolean
    isShowRetype: boolean

    isMatch: boolean

    statusModal: string

    failMessage: string

    changePasswordForm: FormGroup

    old_password = new FormControl('')
    new_password = new FormControl('')
    retype_password = new FormControl('')

    constructor(
        public dialogRef: MatDialogRef<DialogChangePassword>,
        @Inject(MAT_DIALOG_DATA) public data: any,
        private utilityService: UtilityService,
        private changePasswordService: ChangePasswordService,
        private formBuilder: FormBuilder,
        private router: Router
    ) {
        this.isShowOldPassword = false
        this.isShowNewPassword = false
        this.isShowRetype = false

        this.isMatch = false

        this.statusModal = "changepassword"

        this.changePasswordForm = this.formBuilder.group({
            old_password: this.old_password,
            new_password: this.new_password,
            retype_password: this.retype_password
        })
    }

    matchesPassword() {
        if (this.new_password.value != this.retype_password.value) {
            this.isMatch = false
        } else {
            this.isMatch = true
        }
    }

    timeoutCondition(hitApi: any, timeout: any) {
        hitApi.unsubscribe()
        clearTimeout(timeout)

        this.statusModal = "failed"

        this.failMessage = "Timeout, Mohon periksa koneksi internet anda"
    }

    changePasswordFunc() {
        let ParamReq = {
            user_id: this.utilityService.user_id,
            current_password: this.old_password.value,
            new_password: this.new_password.value
        }

        this.statusModal = "loading"

        let changePassword = this.changePasswordService.changePassword(ParamReq)
            .subscribe((result: any) => {
                clearTimeout(timeout)

                if (result.status == 200) {
                    this.statusModal = "success"
                } else if (result.status == 401) {
                    this.statusModal = "failedBack"

                    this.failMessage = result.message
                } else {
                    this.statusModal = "failedBack"

                    this.failMessage = result.message
                }
            }, _error => {
                clearTimeout(timeout)

                if (_error.error.code == 0) {
                    this.statusModal = "failed"

                    this.failMessage = "Maaf, tidak dapat menghubungkan ke server"
                } else if (_error.status == 400) {
                    this.dialogRef.close()

                    this.router.navigate(['/login'])
                } else if (_error.status == 401) {
                    this.statusModal = "failedBack"

                    this.failMessage = _error.error.message
                } else {
                    this.statusModal = "failed"

                    this.failMessage = "Maaf, sepertinya telah terjadi kesalahan server, mohon coba beberapa saat lagi"
                }
            })

        let timeout = setTimeout(() => {
            this.timeoutCondition(changePassword, timeout)
        }, 10000)
    }

    backToIdle() {
        this.statusModal = "changepassword"
    }

}