import { Component, ViewEncapsulation, Inject } from "@angular/core"
import { fuseAnimations } from "@fuse/animations"
import { FormGroup, FormControl, FormBuilder } from "@angular/forms"
import { MatDialogRef, MAT_DIALOG_DATA } from "@angular/material"

import { UtilityService } from "app/services/utility.service"
import { ForgotPasswordService } from "app/services/forgot-password.service"

@Component({
    selector: 'dialog-forgot-password',
    templateUrl: 'dialog-forgot-password.html',
    providers: [UtilityService, ForgotPasswordService],
    encapsulation: ViewEncapsulation.None,
    animations: fuseAnimations
})
export class DialogForgotPassword {
    isShowPassword: boolean
    isShowRetype: boolean

    isMatch: boolean

    statusModal: string
    titleDialog: string
    messageDialog: string
    buttonDialog: string

    requestOTPForm: FormGroup
    submitOTPForm: FormGroup
    newPasswordForm: FormGroup

    email = new FormControl('')
    phone_number = new FormControl('')

    otp_code = new FormControl('')

    password = new FormControl('')
    retype_password = new FormControl('')

    constructor(
        public dialogRef: MatDialogRef<DialogForgotPassword>,
        @Inject(MAT_DIALOG_DATA) public data: any,
        private formBuilder: FormBuilder,
        private forgotPasswordService: ForgotPasswordService
    ) {
        this.isShowPassword = false
        this.isShowRetype = false

        this.isMatch = false

        this.statusModal = "reqOTPDialog"

        this.requestOTPForm = this.formBuilder.group({
            email: this.email,
            phone_number: this.phone_number
        })

        this.submitOTPForm = this.formBuilder.group({
            otp_code: this.otp_code
        })

        this.newPasswordForm = this.formBuilder.group({

        })
    }

    successCondition(timeout: any, title: string, message: string, button: string) {
        clearTimeout(timeout)

        this.statusModal = "successDialog"

        this.titleDialog = title
        this.messageDialog = message
        this.buttonDialog = button
    }

    errorCondition(timeout: any, title: string, message: string, button: string) {
        clearTimeout(timeout)

        this.statusModal = "errorDialog"

        this.titleDialog = title
        this.messageDialog = message
        this.buttonDialog = button
    }

    timeoutCondition(hitApi: any, timeout: any, title: string, message: string, button: string) {
        hitApi.unsubscribe()
        this.errorCondition(timeout, title, message, button)
    }

    reqOTPFunc() {
        this.statusModal = "loadingDialog"

        let ParamReq = {
            key_id: "4OeCn74wA+VjRr2ZJTkHMCXRnh9gAVnMsLKKwoo6wjjLYdUNLZb9nFFwKJE=",
            email: this.email.value,
            phone_number: this.phone_number.value,
        }

        let reqOTP = this.forgotPasswordService.reqOTP(ParamReq)
            .subscribe((result: any) => {
                if (result.status == 200) {
                    clearTimeout(timeout)

                    this.statusModal = "submitOTPDialog"
                } else if (result.status == 403) {
                    this.errorCondition(timeout, "Pesan", "Maaf, Nomor dan Email tidak sesuai", "Kembali")
                } else {
                    this.errorCondition(timeout, "Pesan", result.message, "Kembali")
                }
            }, _error => {
                if (_error.status == 0) {
                    this.errorCondition(timeout, "Pesan", "Maaf, tidak dapat menghubungkan ke server", "Kembali")
                } else {
                    this.errorCondition(timeout, "Pesan", "Maaf, sepertinya telah terjadi kesalahan server, mohon coba beberapa saat lagi", "Kembali")
                }
            })

        let timeout = setTimeout(() => {
            this.timeoutCondition(reqOTP, timeout, "Pesan", "Timeout, Mohon periksa koneksi internet anda", "Kembali")
        }, 10000)
    }

    submitOTPFunc() {
        this.statusModal = "loadingDialog"

        let ParamReq = {
            key_id: "4OeCn74wA+VjRr2ZJTkHMCXRnh9gAVnMsLKKwoo6wjjLYdUNLZb9nFFwKJE=",
            email: this.email.value,
            phone_number: this.phone_number.value,
            otp_code: this.otp_code.value
        }

        let submitOTP = this.forgotPasswordService.submitOTP(ParamReq)
            .subscribe((result: any) => {
                if (result.status == 200) {
                    clearTimeout(timeout)

                    this.statusModal = "newPasswordDialog"
                } else if (result.status == 403) {
                    this.errorCondition(timeout, "Pesan", "Maaf, Nomor OTP yang dimasukan tidak sesuai", "Kembali")
                } else {
                    this.errorCondition(timeout, "Pesan", result.message, "Kembali")
                }
            }, _error => {
                if (_error.status == 0) {
                    this.errorCondition(timeout, "Pesan", "Maaf, tidak dapat menghubungkan ke server", "Kembali")
                } else {
                    this.errorCondition(timeout, "Pesan", "Maaf, sepertinya telah terjadi kesalahan server, mohon coba beberapa saat lagi", "Kembali")
                }
            })

        let timeout = setTimeout(() => {
            this.timeoutCondition(submitOTP, timeout, "Pesan", "Timeout, Mohon periksa koneksi internet anda", "Kembali")
        }, 10000)
    }

    matchesPassword() {
        if (this.password.value != this.retype_password.value) {
            this.isMatch = false
        } else {
            this.isMatch = true
        }
    }

    newPassword() {
        this.statusModal = "loadingDialog"

        let ParamReq = {
            key_id: "4OeCn74wA+VjRr2ZJTkHMCXRnh9gAVnMsLKKwoo6wjjLYdUNLZb9nFFwKJE=",
            email: this.email.value,
            phone_number: this.phone_number.value,
            new_password: this.password.value
        }

        let newPassword = this.forgotPasswordService.newPassword(ParamReq)
            .subscribe((result: any) => {
                if (result.status == 200) {
                    this.successCondition(timeout, "Pesan", "Password telah berhasil diubah, Silahkan Login dengan password baru anda", "Oke")
                } else {
                    this.errorCondition(timeout, "Pesan", result.message, "Kembali")
                }
            }, _error => {
                if (_error.status == 0) {
                    this.errorCondition(timeout, "Pesan", "Maaf, tidak dapat menghubungkan ke server", "Kembali")
                } else {
                    this.errorCondition(timeout, "Pesan", "Maaf, sepertinya telah terjadi kesalahan server, mohon coba beberapa saat lagi", "Kembali")
                }
            })

        let timeout = setTimeout(() => {
            this.timeoutCondition(newPassword, timeout, "Pesan", "Timeout, Mohon periksa koneksi internet anda", "Kembali")
        }, 10000)
    }

    goForgotPasswordDialog() {
        this.statusModal = "reqOTPDialog"
    }

}